import axios from 'axios';
const baseUrl='/api'
const get = (url) => {
    console.log(url)
   return axios.get(`${baseUrl}${url}`).then(x=>x.data)
}
const post = (url, data) => {
    return axios.post(`${baseUrl}${url}`, data)
}
const del = (url, data) => {
    return axios.delete(`${baseUrl}${url}`)
}
const put = (url, data) => {
    return axios.post(`${baseUrl}${url}`, data)
}

export {get, post, del, put};
